








































































@import './../../style/custom.styl'
.skeleton-box
    height: 100%;
    overflow: hidden;
.skeleton-item
    background: #fff;
    .skeleton-item-body
        box-sizing: border-box;
        padding: 15px 0;
        margin: 0 15px;
        .skeleton-info
            flex: auto;
.border-bottom
    border-bottom: 1px solid $borderColor;
