.icon-search
    padding: 5px;
    font-size: 20px;

.food-type-list
    margin-bottom: 10px;
    height: 200px;
    background: #fff;

.food-type-slid
    display: flex;
    flex-wrap: wrap;

.food-type-item
    flex: 0 0 25%;
    padding: 10px 0;
    font-size: 14px;
    color: $blank;
    text-decoration: none;
    img[lazy=loading] 
        width: 29px;
        height: 29px;
        padding: 8px;
        border-radius: 50%;
        overflow: hidden;
        background-color: #efefef;

.food-type-img
    margin-bottom: 6px;
    width: 45px;
    height: 45px;

.food-type-title
    text-align: center;
